<!--:description="` ${successCount} \n ${successRate} % `"-->

<template>
  <div id="app-footer">
    <hr />
    <social-sharing
      :title="title"
      url="https://sakuramochi0.github.io/kinpri-rush-minigame-simulator/"
      hashtags=""
      inline-template
      :is-results-given="isResultsGiven"
    >
      <div>
        <network network="twitter">
          <button class="button twitter">
            <b-icon icon="twitter" />
            <span></span>
          </button>
        </network>
      </div>
    </social-sharing>
  </div>
</template>

<script>
import BIcon from 'buefy/src/components/icon/Icon';

export default {
  name: 'AppFooter',
  components: { BIcon },
  props: ['successCount', 'successRate'],
  computed: {
    isResultsGiven() {
      // noinspection JSValidateTypes
      return this.successCount !== undefined && this.successRate !== undefined;
    },

    title() {
      const commonText =
        '  | kinpri-rush-minigame-simulator';

      if (this.isResultsGiven) {
        return ` ${
          this.successCount
        }   ${
          this.successRate
        } % \n\n${commonText}`;
      }

      return commonText;
    },
  },
};
</script>

<style scoped>
#app-footer {
  margin-bottom: 1.5rem;
}
</style>



